import { useEffect, useRef, useState } from "react";
import { matchesLabel } from "../../scripts/id-util";
import SearchResults from './SearchResults';

const Contraindications = ({ self: solution, display }) => {
    if (!solution.contraindications) solution.contraindications = [];

    const [num, setNum] = useState(0);

    useEffect(() => {
        setNum(solution.contraindications.length);
    }, [solution.contraindications.length]);

    return (
        <div className={`contraindications ${display ? "" : "hidden"}`}>
            <p className="mdb-block-heading">Contraindications:</p>
            {[...Array(num)].map((_, i) => (
                <ContraindicatedItem  key={`contraindication=${i}`} self={solution} num={i} />
            ))}
            <button type="button" onClick={() => setNum(num + 1)}>New Contraindication</button>
        </div>
    );
}

export default Contraindications;

const ContraindicatedItem = ({ self: solution, num }) => {
    const [render, setRender] = useState(true);
    const [query, setQuery] = useState();
    const idRef = useRef(); 

    if (!solution.contraindications[num] && render) 
        solution.contraindications[num] = { id: "", phase: "" };

    const self = solution.contraindications[num];

    const updateSelf = (e) => {
        self[e.target.name] = e.target.value;
    }

    const deleteSelf = () => {
        setRender(false);
        solution.contraindications.splice(num, 1);
    }

    const setValue = (e) => { 
        idRef.current.value = e.target.dataset.label; 
        self.label = e.target.dataset.label;
        self.id = e.target.dataset.id;
        setQuery(null); 
    }

    if (!render) return null;

    return (
        <div className="inputs">
            <label>
                ID:
                <input type="text" 
                    ref={idRef}
                    name="id"
                    defaultValue={self.label}
                    onChange={e => setQuery(e.target.value)}
                />
                <SearchResults list={matchesLabel(query)} setVal={setValue} />
            </label>
            <label>
                Phase:
                <input type="text" 
                    name="phase"
                    defaultValue={self.phase}
                    onChange={updateSelf} />
            </label>
            <button type="button" onClick={deleteSelf}>-</button>
        </div>
    );
}