import { useEffect, useRef, useState } from 'react';

import { getDocumentWithName, saveDocument } from '../../scripts/connect-firebase.js';
// import { CHECKLIST_META_DATA } from '@meddbriefer/scenario-data/traumaScenarioV2MetaData';
import { MDB_TYPES as MDB } from '@meddbriefer/scenario-data/constants.js';
import MetaItem from './MetaItem';
import Hierarchy from './Hierarchy.js';
import Medications from '../Medications/Medications.js';

const COLLECTION_ID = "scenario-types";
const DOCUMENT_NAME = "trauma";

const MetaDataEditor = () => {
    const idRef = useRef();
    const [data, setData] = useState();
    const [meds, setMeds] = useState();
    const [view, setView] = useState(MDB.PHASE);
    const [phaseTypes, setPhaseTypes] = useState({ subPhase: [], checkItem: [] });

    useEffect(() => {
        // Load checklist metadata and hierarchy
        getDocumentWithName(COLLECTION_ID, DOCUMENT_NAME)
            .then(metadata => {
                idRef.current = metadata.id;
                setData(metadata.checklist);
                // setData(CHECKLIST_META_DATA);
                setMeds(metadata.medications);

                // Update phase type exclusions
                let types = { subPhase: [], checkItem: [] };
                metadata.checklist.forEach((item, i) => {
                    if (item.type === MDB.SUB_PHASE)
                        types.subPhase[i] = item.relation;
                    else if (item.type !== MDB.PHASE)
                        types.checkItem[i] = item.relation;
                });
                setPhaseTypes(types);
            })
            .catch(console.error);
    }, []);

    const isCurrentTab = (id) => view === id ? "selected" : "";
    const isOption = (e) => e.type === MDB.ASSESS_OPTION || e.type === MDB.DECISION_OPTION;

    return (
        <form className="checklist-meta-data" autoComplete="off">
            <div className="tab-selector">
                <Tab text="Phases"      id={MDB.PHASE} />
                <Tab text="Sub-Phases"  id={MDB.SUB_PHASE} />
                <Tab text="Assessments" id={MDB.ASSESS} />
                <Tab text="Assessments With Options" id={MDB.AWO} />
                <Tab text="Required"    id={MDB.REQUIRED} />
                <Tab text="Decisions"   id={MDB.DECISION} />
                <Tab text="Hierarchy"   id="hierarchy" />
                <Tab text="Medications" id="MEDS" />
            </div>

            {meds && view === "MEDS"
                && <Medications medsState={[meds, setMeds]}/>}

            {data ? data.map((e, i) => (
                !isOption(e) &&
                    <MetaItem 
                        key={e.id} 
                        data={data}
                        item={e} 
                        display={e.type === view} 
                        index={i} 
                        updateData={setData}
                        phaseState={[phaseTypes, setPhaseTypes]}
                        />
            )) : <p>Loading...</p> }

            <Hierarchy 
                dataState={[data, setData]} 
                display={view === "hierarchy"}
                phaseTypes={phaseTypes} />

            {(view === "MEDS") &&
                <button type="button" onClick={upload}>Submit</button>}
        </form>
    );

    async function upload() {
        // Build hierarchy from bottom up (options -> items -> subPhases -> phases)
        let phases = data.filter(e => e.type === MDB.PHASE).slice().map(e => ({...e}));
        let subPhases = data.filter(e => e.type === MDB.SUB_PHASE).slice().map(e => ({...e}));
        let optionParents = data.filter(e => e.type === MDB.AWO || e.type === MDB.DECISION).slice().map(e => ({...e}));
        
        optionParents.forEach(item => item.options = data.filter(e => item.id === e.promptID));
        subPhases.forEach(item => item.items = data.filter(e => item.id === e.relation));
        phases.forEach(item => item[phaseTypes.subPhase.includes(item.id) ? "subPhases" : "items"] = data.filter(e => item.id === e.relation));    

        phases.forEach(phase => phase.items?.length === 0 ? delete phase.items : delete phase.subPhase); // delete extra

        const metaData = { 
            id: idRef.current, 
            name: DOCUMENT_NAME, 
            checklist: data, 
            hierarchy: phases,
            medications: meds,
            schemaVersion: "V2"
        };

        console.log(metaData);

        (window.confirm("Upload changes?")) 
            && await saveDocument(COLLECTION_ID, metaData);

        return metaData;
    }

    function Tab({ text, id }) {
        return (
            <span className={`tab ${isCurrentTab(id)}`} 
                onClick={() => setView(id)}>{ text }</span>
        );
    };
}

export default MetaDataEditor;