import React from "react";
import { HashRouter as Router, Switch, Route } from "react-router-dom";

import { AuthProvider, StaffRoute } from "@meddbriefer/mdb-auth";

import "./styles/default-styles.scss";
import "./styles/home-styles.scss";
import "./styles/scenario-editor-styles.scss";
import "./styles/class-codes-styles.scss";
import "./styles/problems-styles.scss";
import "./styles/tab-styles.scss"
import "./styles/meta-data-styles.scss";
import "./styles/medications-styles.scss";

import UnauthedLayout from "./components/UnauthedLayout"
import Header from "./components/Header";

// import BogusPage from "./components/BogusPage";

import HomePage from "./components/HomePage";
import NewScenario from "./components/ScenarioEditor/NewScenario";
import EditScenario from "./components/ScenarioEditor/EditScenario";
import CloneScenario from "./components/ScenarioEditor/CloneScenario";
import ScenariosList from "./components/ScenarioEditor/ScenariosList";
import ClassCode from './components/ClassCodes/ClassCode';
import ClassCodesList from './components/ClassCodes/ClassCodesList';
import Problems from './components/Problems/ProblemsForm';
import ProblemsFormList from './components/Problems/ProblemsFormList';
import ActionAssociations from './components/Problems/ActionAssociations';
import MetaDataEditor from "./components/MetaData/MetaDataEditor";
import ScenarioTypeSelector from "./components/MetaData/ScenarioTypeSelector";

const App = () => {
  return (
	<AuthProvider>
        <Router>
	  		<Header />
			<Switch>
				<Route exact path="/" component={HomePage} />
				{/* <Route exact path="/tabs-test" component={BogusPage} /> */}
				<StaffRoute exact path="/scenarios" authFlowLayout={UnauthedLayout} component={ScenariosList} />
				<StaffRoute exact path="/scenario/" authFlowLayout={UnauthedLayout} component={NewScenario} />
				<StaffRoute exact path="/scenario/:scenarioId" authFlowLayout={UnauthedLayout} component={EditScenario} />
				<StaffRoute exact path="/scenario/:scenarioId/clone" authFlowLayout={UnauthedLayout} component={CloneScenario} />
				<StaffRoute exact path="/class-codes-list/" authFlowLayout={UnauthedLayout} component={ClassCodesList} />
				<StaffRoute exact path="/class-codes/" authFlowLayout={UnauthedLayout} component={ClassCode} />
				<StaffRoute exact path="/class-codes/:classCode" authFlowLayout={UnauthedLayout} component={ClassCode} />
				<StaffRoute exact path="/problem-entry-list/" authFlowLayout={UnauthedLayout} component={ProblemsFormList} />
				<StaffRoute exact path="/problem-entry/:mdbFormId" authFlowLayout={UnauthedLayout} component={Problems} />
				<StaffRoute exact path="/problem-entry/" authFlowLayout={UnauthedLayout} component={Problems} />
				<StaffRoute exact path="/associations/:formId" authFlowLayout={UnauthedLayout} component={ActionAssociations} />
				<StaffRoute exact path="/scenario-types" authFlowLayout={UnauthedLayout} component={ScenarioTypeSelector} />
				<StaffRoute exact path="/checklist-meta-editor" authFlowLayout={UnauthedLayout} component={MetaDataEditor} />
			</Switch>
        </Router>
	</AuthProvider>
  );
};

export default App;
