import { useRef, useState, useEffect } from "react";
import { matchesLabel } from "../../scripts/id-util";
import SearchResults from './SearchResults';

const Actions = ({ self: solution, display }) => {
    if (!solution.actions) solution.actions = [];

    const [num, setNum] = useState(0);

    useEffect(() => {
        setNum(solution.actions.length);
    }, [solution.actions.length]);

    return (
        <div className={`actions ${display ? "" : "hidden"}`}>
            <p className="mdb-block-heading">Actions:</p>
            {[...Array(num)].map((_, i) => (
                <ActionItem key={`action-${i}`} self={solution} num={i} />
            ))}
        <button type="button" onClick={() => setNum(num + 1)}>New Action</button>
        </div>
    );
}

export default Actions;

const ActionItem = ({ self: solution, num }) => {
    const [render, setRender] = useState(true);
    const [query, setQuery] = useState();
    const idRef = useRef();

    if (!solution.actions[num] && render) 
        solution.actions[num] = { id: "", phase: "", why: "", optional: false, minimal: false };

    const self = solution.actions[num];

    const updateSelf = (e) => {
        e.target.type === "text"
            ? self[e.target.name] = e.target.value
            : self[e.target.name] = e.target.checked;
    }

    const deleteSelf = () => {
        setRender(false);
        solution.actions.splice(num, 1);
    }

    const setValue = (e) => { 
        idRef.current.value = e.target.dataset.label; 
        self.label = e.target.dataset.label;
        self.id = e.target.dataset.id
        setQuery(null); 
    }

    if (!render) return null;

    return (
        <div className="inputs">
            <label>
                ID:
                <input type="text" 
                    name="id"
                    ref={idRef}
                    defaultValue={self.label}
                    onChange={e => setQuery(e.target.value)}
                />
                <SearchResults list={matchesLabel(query)} setVal={setValue} />
            </label>
            <label>
                Phase:
                <input 
                    onChange={updateSelf}
                    defaultValue={self.phase}
                    name="phase"
                    type="text" />
            </label>
            <label>
                Why:
                <input 
                    onChange={updateSelf} 
                    defaultValue={self.why}
                    name="why"
                    type="text" />
            </label>

            <label>
                Optional:
                <input
                    onChange={updateSelf}
                    defaultChecked={self.optional}
                    name="optional"
                    type="checkbox" />
            </label>
            <label>
                Minimal:
                <input
                    onChange={updateSelf}
                    defaultChecked={self.minimal}
                    name="minimal"
                    type="checkbox" />
            </label>
        <button type="button" onClick={deleteSelf}>-</button>
        </div>
    );
}