import { useState } from 'react';

export default function Medication({ med, index, medState }) {
    const [meds, setMeds] = medState;
    const [self, setSelf] = useState(med);

    function update(e) {
        const item = e.target;
        let updated = {...self};

        (item.type === "text")
            ? updated[item.name] = item.value
            : updated[item.name] = item.checked;

        setSelf(updated);

        meds[index] = updated;
        setMeds(meds.slice());
    }

    function deleteSelf() {
        meds.splice(index, 1);
        setMeds(meds.slice());
    }

    return (
        <tr className="medication">
            <td>
                <button type="button" className="delete-bttn" onClick={deleteSelf}>-</button>
                &nbsp;
            </td>
            <td className="medication-header">
                <h3>{self.label ?? "New Medication"}</h3>
            </td>
            <td>
                <input 
                    type="text" 
                    className="id" 
                    id={`${self.id}-id`} 
                    placeholder="ID" 
                    name="id"
                    onChange={update}
                    defaultValue={med.id}
                />
            </td>
            <td>
                <input 
                    type="text" 
                    className="label" 
                    id={`${self.id}-label`} 
                    placeholder="Label" 
                    name="label"
                    onChange={update}
                    defaultValue={med.label}
                />
            </td>
            <td>
                <input 
                    type="checkbox" 
                    id={`${self.id}-isPainMed`} 
                    name="isPainMed" 
                    defaultChecked={med.isPainMed} 
                    onChange={update} />
            </td>
            <td>
                <input 
                    type="checkbox" 
                    id={`${self.id}-isAirwayMed`} 
                    name="isAirwayMed" 
                    defaultChecked={med.isAirwayMed} 
                    onChange={update} />
            </td>
            <td>
                <input 
                    type="checkbox" 
                    id={`${self.id}-isFluid`} 
                    name="isFluid" 
                    defaultChecked={med.isFluid} 
                    onChange={update} />
            </td>
        </tr>
    );
}