const getApp = (appID) => {
    const matches = firebase.apps.filter(app => app.options.appId === appID)
    // console.log("matches", matches)
    return (matches.length === 1) ? matches[0] : null
}

const getOrInitializeApp = (appCfg, name) => {
    const app = getApp(appCfg.appId)
    // console.log(`${appCfg.appId} already exists: ${!!app}`)
    return (!!app) ? app : firebase.initializeApp(appCfg, name)
}

