import classnames from "classnames"

const CriticalActions = ({ active, scenario }) => {

    return (
        <div className={classnames("scenario-form", { "scenario-view-hidden": !active })}>
            <div id="critical-actions" className="info-wrapper">
                <span>Placeholder for Critical Actions</span>
            </div>
        </div>
    )
}

export default CriticalActions