import classnames from "classnames"

const InterventionAnswers = ({ active, scenario }) => {

    return (
        <div className={classnames("scenario-form", { "scenario-view-hidden": !active })}>
            <div id="intv-answers" className="info-wrapper">
                {undefined === scenario.criticalActions
                    ? <span>You need to add Critical Actions prior to associating correct answers to their interventions</span>
                    : <span>Placeholder for Intervention Answers</span>
                }
            </div>
        </div>
    )
}

export default InterventionAnswers