import { useState } from 'react';
import { MDB_TYPES as MDB } from '@meddbriefer/scenario-data/constants.js';

const MetaItem = ({ data, item, display, index, updateData, phaseState }) => {
    const [phaseTypes, setPhaseTypes] = phaseState;
    const [itemID, setItemID] = useState(item.id);
    const optionType = item.type === MDB.DECISION ? MDB.DECISION_OPTION : MDB.ASSESS_OPTION;

    
    const hasOptions = item.type === MDB.AWO || item.type === MDB.DECISION;
    const [options, setOptions] = useState( data.filter(e => e.promptID === item.id) );
    
    const deleteSelf = () => {
        data.splice(index, 1);
        updateData(data.slice());
    }
    
    const deleteOption = (e) => e.target.parentNode.remove();
    
    const insertItem = () => {
        data.splice(index + 1, 0, { id: null, type: item.type });
        updateData(data.slice());
    }
    
    const updateSelf = (e) => {
        if (e.target.name === "id")
        setItemID(e.target.value);
        data[index][e.target.name] = e.target.value;
        updateData(data.slice());
    }
    
    const setPhaseType = (e) => {
        // Phase type exclusions 
        if (item.type === MDB.SUB_PHASE) {
            phaseTypes.subPhase[index] = e.target.value;
            data[index][MDB.PHASE] = e.target.value;
        } else {
            phaseTypes.checkItem[index] = e.target.value;
        }
        
        // Set phase/subPhase fields
        if (e.target.parentNode.label === "Phases") {
            data[index].phase = e.target.value;
        } else {
            data[index].subPhase = e.target.value;
            data[index].phase = data.find(y => y.id === e.target.value).phase;
        }
        
        setPhaseTypes({...phaseTypes});
    }

    const swapUp = () => {
        for (let i=index - 1; i >= 0; i--) {
            if (data[i].type === data[index].type) {
                let temp = data[index];
                data[index] = data[i];
                data[i] = temp;
                updateData(data.slice());
                break;
            }
        }
    }
    
    const swapDown = () => {
        for (let i=index + 1; i < data.length; i++) {
            if (data[i].type === data[index].type) {
                let temp = data[index];
                data[index] = data[i];
                data[i] = temp;
                updateData(data.slice());
                break;
            }
        }
    }

    const phases = data.filter(e => e.type === MDB.PHASE);
    const subphases = data.filter(e => e.type === MDB.SUB_PHASE);

    return (
        <div className={`meta-block ${item.type} ${display ? "" : "hidden"}`} id={`${item.type}-${index}`}>
            <fieldset className="meta-item" classtype={item.type} >
                <button type="button" onClick={insertItem}>+</button>
                <button type="button" className="swap-bbtn" onClick={swapUp}>↑</button>
                <button type="button" className="swap-bbtn" onClick={swapDown}>↓</button>
                <input type="text" name="id" placeholder="Id" defaultValue={item.id} onChange={updateSelf}/>
                <input type="text" name="label" placeholder="Label" defaultValue={item.label} onChange={updateSelf} />
                <input type="text" name="abbrLabel" placeholder="Label Abbreviation" defaultValue={item.abbrLabel} onChange={updateSelf} />
                <input type="hidden" name="type" defaultValue={item.type} />

                { // Relations
                (item.type !== MDB.PHASE) && 
                <select name="relation" value={item.relation} onChange={updateSelf}>
                    <option hidden>Relation</option>

                    <optgroup label="Phases">
                    {phases.map((e, i) => (
                        // Check that phases with sub phases only have sub-phases
                        // and phases with assessments/decisions only have assessments/decisions
                        e.id && e.label &&
                        !(item.type !== MDB.SUB_PHASE && phaseTypes.subPhase.includes(e.id)) &&
                        !(item.type === MDB.SUB_PHASE && phaseTypes.checkItem.includes(e.id)) &&
                        <option key={`${e.id}-${i}`} value={e.id} onClick={setPhaseType}>{e.label}</option> 
                    ))}
                    </optgroup>

                    {item.type !== MDB.SUB_PHASE &&
                    <optgroup label="Sub-Phases">
                    {subphases.map((e, i) => (
                        e.id && e.label &&
                        <option key={`${e.id}-${i}`} value={e.id} onClick={setPhaseType}>{e.label}</option> 
                        ))}
                    </optgroup>}
                </select>}

                <button type="button" onClick={deleteSelf}>-</button>
                {hasOptions && <button type="button" onClick={() => setOptions([...options, {}])}>+</button>}
            </fieldset>

            { // Options
            hasOptions &&
            options.map((child, i) => (
                <fieldset className="meta-item-option" key={child.id ?? i}>
                    <p>Option:</p>
                    <input type="text" name="id" placeholder="Id" required defaultValue={child.id} />
                    <input type="hidden" name="type" value={optionType} />
                    <input type="text" name="label" placeholder="Label" required defaultValue={child.label} />
                    <input type="text" name="abbrLabel" placeholder="Label Abbreviation" required defaultValue={child.abbrLabel} />
                    <input type="hidden" name="promptID" value={itemID ?? ""} />
                    <button type="button" onClick={deleteOption}>-</button>
                </fieldset>
            ))}
        </div>
    );
}

export default MetaItem;