import { useRef } from "react";

// import { MEDS } from '@meddbriefer/scenario-data/traumaScenarioV2MetaData.js';
import Medication from "./Medication";

export default function Medications({ medsState }) {
    const [meds, setMeds] = medsState;
    const formRef = useRef();

    function addMed() {
        setMeds([...meds, {}]);
    }

    return (
        <>
        <table className="medications-form" ref={formRef}>
            <thead>
                <tr>
                    <th>&nbsp;</th>
                    <th>Medication</th>
                    <th>Id</th>
                    <th>Label</th>
                    <th>Is Pain Medication?</th>
                    <th>Is Airway Medication?</th>
                    <th>Is Fluid?</th>
                </tr>
            </thead>
            <tbody className="medications-table">
                {meds
                && meds.map((med, i) => (
                    <Medication key={med.id} med={med} index={i} medState={[meds, setMeds]} />
                ))}
            </tbody>            
        </table>
        <button type="button" onClick={addMed}>New</button>
        </>
    );
}