const mergeGenericTraumaScenarioData = (scenario) => {
    // this is truama-scenario generic data
    scenario.intvMetaData = deepCopy(INTV_META_DATA)
    scenario.interventionMenus = deepCopy(INTV_MENUS)
    scenario.systemInterventions = deepCopy(SYSTEM_INTVS)
    // TODO: perform some scenario-specific substitutions here
    scenario.interventionForms = deepCopy(INTV_FORMS)
    // this will be replaced by scenarioType.hierarchy
    scenario.phaseData = deepCopy(PHASE_DATA)

    return scenario
}

const reconstructChecklistMetaData = (scenario) => {
    const checkListMetaData = []
    const subPhasePhases = {}

    const addFlattenedItem = (item, phase, subphase) => {
        const obj = Object.assign({}, item)
        delete obj.items
        delete obj.dataEntry
        obj.phase = phase
        obj.subPhase = (!!subphase) ? subphase : null
        checkListMetaData.push(obj)
        // item.items are related assess-options or decision-options
        item.items?.map(option => {
            option.phase = phase
            option.subPhase = (!!subphase) ? subphase : null
            checkListMetaData.push(option)
        })
    }

    scenario.phaseData.forEach(rec => {
        if (rec.type === TYPES.PHASE) {
            const phaseID = rec.id
            const phaseObj = Object.assign({}, rec)
            delete phaseObj.items
            delete phaseObj.subPhases
            delete phaseObj.dataEntry
            checkListMetaData.push(phaseObj)
            rec.items?.forEach(item => addFlattenedItem(item, phaseID, null))
            rec.subPhases?.forEach(sp => subPhasePhases[sp.id] = phaseID)
        } else if (rec.type === TYPES.SUB_PHASE) {
            const subPhase = rec.id
            const phase = subPhasePhases[subPhase]
            const subPhaseObject = Object.assign({}, rec)
            delete subPhaseObject.items
            delete subPhaseObject.dataEntry
            subPhaseObject.phase = phase
            checkListMetaData.push(subPhaseObject)
            rec.items.forEach(item => addFlattenedItem(item, phase, subPhase))
        }
    })
    scenario.checkListMetaData = checkListMetaData
    return scenario
}

export const getMetaData = async (db, scenarioType, schemaVersion) => {
    if ("trauma" !== scenarioType) {
        throw(`ERROR: scenario type: ${scenarioType} isn't supported`)
    }
    switch (schemaVersion) {
        case "V0":
            return { "CHECKLIST_META_DATA": CMDV0, "INTV_META_DATA": IMDV0 }
        case "V1":
            return { "CHECKLIST_META_DATA": CMDV1, "INTV_META_DATA": IMDV1 }
        case "V2":
            return { "CHECKLIST_META_DATA": CMDV2, "INTV_META_DATA": IMDV2 }
        default:
            return Promise.reject(new Error(`ERROR: unknown schema version: ${schemaVersion}`))
    }
}

export const getInterventionForms = async (db, scenarioType, schemaVersion) => {
    if ("trauma" !== scenarioType) {
        return Promise.reject(new Error(`ERROR: scenario type: ${scenarioType} isn't supported`))
    }
    if (schemaVersion !== "V2") {
        return {}
    } else {
        return INTV_FORMS
    }
}

export const getCriticalActions = async (db, scenario, schemaVersion) => {
    if (scenario !== "PHTC M2CA") {
        return Promise.reject(new Error(`ERROR: scenario ${scenario} not found`))
    }
    if (schemaVersion !== "V0" && schemaVersion !== 'V2') {
        return Promise.reject(new Error(`ERROR: currently no critical actions for schema version ${schemaVersion}`))
    }
    return criticalActions
}

