import React, { useState, useRef, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import { useDebug } from '../../scripts/custom-hooks';

import Problem from './Problem';
import { verifyDocument, getDocument, saveDocument } from '../../scripts/connect-firebase.js';

const COLLECTION_ID = 'problem-solutions';

export const ProblemsContext = React.createContext({});

const FormRoot = (props) => {
    const [form, setForm] = useState({problems: [{}]});
    const [numProblems, setNumProblems] = useState(1);
    const { mdbFormId: formId } = props.match.params;
    const isEdit = () => !!formId;
    const idRef = useRef();
    let history = useHistory();

    useEffect(() => {
        formId
        && getDocument(COLLECTION_ID, formId)
            .then(doc => {
                setNumProblems(doc.problems.length);
                setForm(doc);
                idRef.current = doc.id;
            })
            .catch(console.error)
    }, [formId]);

    useDebug(() => console.log(form));

    const addProblem = () => {
        setNumProblems(numProblems + 1);
        form.problems.push({});
    }

    const updateSelf = (e) => { form[e.target.name] = e.target.value; }

    if (!form) return <p>Loading...</p>;

    return (
        <ProblemsContext.Provider value={form}>
            <div className="mdb-form-wrapper">
                <form id="debriefer-form" className="debriefer-form" autoComplete="off" >
                    <label className="mdb-block-heading">
                        Scenario Name:
                        <input type="text" 
                            className="mdb-text" 
                            name="scenarioName" 
                            onChange={updateSelf}
                            defaultValue={form.scenarioName} />
                    </label>

                    {[...Array(numProblems)].map( (_, i) => (
                        <Problem key={`block-${i}`} problem={i} />
                    ))}

                    <button type="button" onClick={addProblem} >New Problem</button>
                </form>

                <button type="button" id="submit-bttn" onClick={process}>Continue</button>
            </div>
        </ProblemsContext.Provider>
    );

    async function process() {
        let object = {
            id: idRef.current,
            schemaVersion: "V2",
            ...form
        }

        //Validate & upload object
        if (object.scenarioName.trim() === "" || 
            (!isEdit && 
            !await verifyDocument(COLLECTION_ID, formId)
        )) {
            alert("You need a new name for the scenario!");
            return;
        } else {
            if (!window.confirm("Upload?")) return;
            await saveDocument(COLLECTION_ID, object);

            alert(`Problems uploaded.`);
            history.push(`/associations/${object.id}`);
            // history.push("/problem-entry-list/");
        }
    }
}

export default FormRoot;