import { MDB_TYPES as MDB } from "@meddbriefer/scenario-data/constants";

const Hierarchy = ({ dataState, display, hierarchyState, phaseTypes }) => {
    const [data, setData] = dataState;
    const headings = data?.filter(e => e.type === MDB.PHASE || e.type === MDB.SUB_PHASE);
    
    const swapUp = (e) => {
        let index = parseInt(e.target.parentNode.dataset.idx);
        for (let i=index - 1; i >= 0; i--) {
            if (data[i].relation === data[index].relation) {
                let temp = data[index];
                data[index] = data[i];
                data[i] = temp;
                setData(data.slice());
                break;
            }
        }
    }
    
    const swapDown = (e) => {
        let index = parseInt(e.target.parentNode.dataset.idx);
        for (let i=index + 1; i < data.length; i++) {
            if (data[i].relation === data[index].relation) {
                let temp = data[index];
                data[index] = data[i];
                data[i] = temp;
                setData(data.slice());
                break;
            }
        }
    }
    
    return (
        <div className={`meta-data-hierarchy ${display ? "" : "hidden"}`}>
            {headings?.map(phase => (
                <div id={phase.id} key={phase.id} className="hierarchy-block">
                    <h3>{phase.label}</h3>
                    {data.map((item, i) => item.relation === phase.id && (
                        <div data-idx={i} key={item.id} className="hierarchy-item">
                            <button type="button" onClick={swapUp}>↑</button>
                            <button type="button" onClick={swapDown}>↓</button>
                            <p>{item.label}</p>
                        </div>
                    ))}
                </div>
            ))}
        </div>
    );
}

export default Hierarchy;